.splide__gradient {
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;

    width: 20%;
    height: 100%;

    background-image: linear-gradient(to right, transparent, #13171d);
}

.banner__gradient {
    background-image: linear-gradient(to right, #13171d 30%, transparent);
}